<template>
  <div id="app">
    <div id="editor">
      <VJsoneditor v-model="json" :plus="true" @error="onError" />
    </div>
    <div style="margin: 16px;">
      <label for="newline">\n</label>
      <input type="checkbox" id="newline" value="newline" v-model="checkedNames" />
      <label for="yinhao">"</label>
      <input type="checkbox" id="yinhao" value="yinhao" v-model="checkedNames" />
      <label for="tab">\t</label>
      <input type="checkbox" id="tab" value="tab" v-model="checkedNames" />
      <label for="and">&</label>
      <input type="checkbox" id="and" value="and" v-model="checkedNames" />
      <label for="wenhao">?</label>
      <input type="checkbox" id="wenhao" value="wenhao" v-model="checkedNames" />
    </div>
    <div>
      <button v-on:click="escapse" class="simple_button">Escapse</button>
    </div>
    <div>{{ escapseString }}</div>
  </div>
</template>

<script>
import VJsoneditor from "v-jsoneditor/src/index";
import Vue from 'vue'

export default {
  name: "SimpleTools",
  data() {
    return {
      json: {},
      newline: false,
      checkedNames: ["yinhao"],
      escapseString: ""
    };
  },
  components: {
    VJsoneditor
  },
  methods: {
    onError() {},
    escapse() {
      var myJSONString = JSON.stringify(this.json);
      var myEscapedJSONString = "";
      if (this.checkedNames.indexOf("yinhao") >= 0) {
        myEscapedJSONString = myJSONString.replace(/\"/g, '\\"');
      }

      if (this.checkedNames.indexOf("newline") >= 0) {
        myEscapedJSONString = myJSONString.replace(/\\n/g, " ");
      }

      if (this.checkedNames.indexOf("tab") >= 0) {
        myEscapedJSONString = myJSONString.replace(/\\t/g, " ");
      }

      if (this.checkedNames.indexOf("and") >= 0) {
        myEscapedJSONString = myJSONString.replace(/\&/g, "\\&");
      }

      if (this.checkedNames.indexOf("wenhao") >= 0) {
        myEscapedJSONString = myJSONString.replace(/\?/g, "\\?");
      }

      if (myEscapedJSONString.length > 2){
        this.escapseString = myEscapedJSONString;
        this.updateClipboard(this.escapseString);
      } else {
          Vue.$toast.open('2b，输入内容');
      }
    },
    updateClipboard(newClip) {
      navigator.clipboard.writeText(newClip).then(
        function() {
          Vue.$toast.open('copy!');
        },
        function() {
        }
      );
    }
  }
};
</script>

